
import { Component, OnInit } from '@angular/core';
import anime from 'animejs';


@Component({
  selector: 'app-ls-section-list',
  templateUrl: './section-list.component.html',
  styleUrls: ['./section-list.component.scss']
})
export class SectionListComponent implements OnInit {

  constructor() { }
  show = false;
  ngOnInit() {
  }

  toogleCurrentView() {
    if (this.show) {
        anime({
          targets: '.border-none',
          translateX: -100,
          direction: 'alternate',
          loop: false,
          elasticity: function(el, i, l) {
            return (200 + i * 200);
          }
        });
        setTimeout(() => {
          this.show = false;
      }, 500);
    }else {
     this.show = true;
    setTimeout(() => {
      anime({
        targets: '.border-none',
        translateX: 250,
        direction: 'alternate',
        loop: false,
        elasticity: function(el, i, l) {
          return (200 + i * 200);
        }
      });
    }, 0);
  }
  }
}



